<script>
    import {Bar} from "svelte-chartjs";
    import {onMount} from "svelte";
    import Chart from "chart.js/auto";

    let celebrities = [
        {name: "Celebrity 1", height: 170},
        {name: "Celebrity 2", height: 160},
        {name: "Celebrity 3", height: 180},
        {name: "Celebrity 4", height: 150},
        {name: "Celebrity 5", height: 165},
    ];

    let selectedCelebrities = [];
    let chartData = {
        labels: [],
        datasets: [
            {
                label: "Height Difference (cm)",
                data: [],
                backgroundColor: "rgba(75, 192, 192, 0.2)",
                borderColor: "rgba(75, 192, 192, 1)",
                borderWidth: 1,
            },
        ],
    };

    function toggleCelebrity(celebrity) {
        if (selectedCelebrities.includes(celebrity)) {
            selectedCelebrities = selectedCelebrities.filter(c => c !== celebrity);
        } else {
            if (selectedCelebrities.length < 5) {
                selectedCelebrities = [...selectedCelebrities, celebrity];
            } else {
                alert("You can select up to 5 celebrities only.");
            }
        }
        updateChartData();
    }

    function updateChartData() {
        chartData.labels = selectedCelebrities.map(c => c.name);
        if (selectedCelebrities.length > 1) {
            const heights = selectedCelebrities.map(c => c.height);
            const maxHeight = Math.max(...heights);
            chartData.datasets[0].data = heights.map(height => maxHeight - height);
        } else {
            chartData.datasets[0].data = [];
        }
    }

    onMount(() => {
        updateChartData();
    });
</script>

<main>
    <h1>Celebrity Height Comparison</h1>
    <div class="celebrity-list">
        {#each celebrities as celebrity}
            <label>
                <input type="checkbox" on:change={() => toggleCelebrity(celebrity)} />
                {celebrity.name} ({celebrity.height} cm)
            </label>
        {/each}
    </div>
    <button on:click={() => updateChartData()}>Compare Heights</button>
    {#if chartData.datasets[0].data.length > 0}
        <Bar {chartData} />
    {/if}
</main>

<style>
    body {
        font-family: Arial, sans-serif;
    }
    .celebrity-list {
        max-width: 300px;
        margin-bottom: 20px;
    }
    .celebrity-list label {
        display: block;
        margin-bottom: 5px;
    }
</style>
